@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

html{
    background-image: url('https://cdn.midjourney.com/c1ad7a94-1ded-4c8c-81a0-22c8821ae705/grid_0.png');
    background-size: cover;
    font-family: 'Press Start 2P', cursive;
}

.container {
    display: grid; 
    grid-auto-columns: 1fr; 
    grid-auto-rows: 1fr; 
    grid-template-columns: 1fr 1fr 1fr 1fr; 
    grid-template-rows: 0.3fr 0.7fr 1.3fr 0.7fr; 
    gap: 0px 0px; 
    grid-template-areas: 
      "title title title title"
      ". new new ."
      ". unpaid paid ."
      ". total total ."; 
      background-color: antiquewhite;
  }
  .title { 
    grid-area: title;
    background-color: #497174;
    border-style:groove;
    border-width: 5px;
    border-color: #EB6440;
    text-align: center;
 }
  .new { 
    grid-area: new;
    justify-self: center;
 }
  
  .unpaid { 
    grid-area: unpaid;
    justify-self: center;
 }
  
  .paid { 
    grid-area: paid;
    justify-self: center;
 }
  
  .total { 
    grid-area: total;
    justify-self: center;
 }

 table{
    border-spacing: 20px;
    border-collapse:separate ;
    border-style: dotted;
 }
  